h1 {
  text-align: center;
  margin-bottom: 0;
  margin-top: 0;
  font-family: 'Black Han Sans', sans-serif;
  width: auto;
  height: auto;
  padding: 0;
}

.item {
  margin-top: 0;
  margin-bottom: 0;
  border-right: 1px solid gray;
  background-color: #eff3f7;
}

#grid {
  display: grid;
  grid-template-columns: 150px 1fr;
}

.BlogContent {
  padding-left: 30px;
  padding-right: 30px;
}

h2 {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  font-family: 'Black Han Sans', sans-serif;
}

a {
  text-decoration: none;
}

h3 {
  margin-bottom: 0;
}

.imgsituate {
  text-align: center;
}

img {
  border-radius: 50px;
  position: relative;
  right: 20;
}

ul {
  font-family: 'Black Han Sans', sans-serif;
}

.SetColor {
  color: #808080;
}

#mainContainer {
  display: flex;
  flex-direction: row;
  border: 1px solid gray;
  margin-right: 10vw;
  margin-left: 10vw;
}
